@keyframes animate{
	0%{
		opacity: 0;
		fill: rgba(255,255,255,0);
		stroke-dashoffset: 1200;
	}
	30%{
		opacity: 10;
		stroke-dashoffset: 1200; 
	}
	90%{
		fill: rgba(255,255,255,0);
	}
	100%{
		stroke-dashoffset: 0; 
	}
}

@keyframes handwrite{
	100%{
		stroke-dashoffset: 0;
	}
}
@keyframes stroke{
	0%{
		stroke:#f05038;
	}
	100%{
		stroke: white;
	}
}
@keyframes opacity{
	0%{
		opacity: 0;
	}
	100%{
		opacity: 1;
	}
}